/**
 * Default login widget for desktop
 * @author Edward J Morales
 */

bizagi.login.widgets.login.extend("bizagi.login.widgets.login", {}, {
  init: function init(loginFacade, dataService) {
    this._super(loginFacade, dataService);
  },
  postRender: function postRender(params) {
    var self = this;
    var template = self.loginFacade.getTemplate("normal-login");
    var form = "";
    var content = self.getContent();
    self.params = params || {};

    if (params.authenticationType == "BizAgi" || params.authenticationType == "Bizagi" || params.authenticationType == "Mixed" || params.authenticationType == "Windows") {
      var enableOptions = "true";
    }

    $.when(self.dataService.getDomainList()).done(function (domains) {
      /**
                                                                       * @type { { name: string }[] }
                                                                       * @const
                                                                       */
      self.domains = domains.domains;

      // Render Form
      form = $.tmpl(template, {
        domains: self.domains,
        isBizagiAuthentication: enableOptions,
        isAdminLoginRequired: bizagi.util.parseBoolean(self.params.isAdminLoginRequired),
        authenticationType: self.params.authenticationType });


      $("#form-content", content).append(form);

      // Set user value

      var username = self.getParameterByName('b_username');
      if (username) {
        var dataArray = username.split('\\');
        self.removeReadOnly($("#user"), false);

        switch (dataArray.length) {
          case 1:
            $("#user").val(decodeURI(dataArray[0]));
            break;
          case 2:
            $("#user").val(decodeURI(dataArray[1]));

            if (self.domains.length > 1 && self.isValidDomain(decodeURI(dataArray[0]))) {
              $("#domain").val(decodeURI(dataArray[0]));
            } else {
              if (self.domains.length) {
                $("#domain").val(self.domains[0].name);
              } else {
                $("#domain").val('domain');
              }
            }
            break;}

      } else {
        // Set user value
        if (params.user) {
          var userInput = $("#user", content);
          userInput.val(params.user);
          self.removeReadOnly(userInput, true);
        }
        // Set domain value
        if (self.domains.length > 1 && self.isValidDomain(params.domain)) {
          $("#domain").val(params.domain);
        } else {
          if (self.domains.length) {
            $("#domain").val(self.domains[0].name);
          } else {
            $("#domain").val('domain');
          }
        }
      }

      // Check Login Options
      if (self.getParameterByName('b_origin') && self.getParameterByName('b_origin') === 'native') {
        $(".bz-login__options").detach();
      } else if (params.selectedCheck) {
        var loginOption = "";

        if (params.selectedCheck == "saveAccount") {
          loginOption = "rbSaveAccount";
          $("#" + loginOption, ".bz-login__options").attr("checked", "checked");
        } else {
          loginOption = "rbAskAlways";
        }
      }

      // Create object of message controller
      self.messageController = new bizagi.login.message(self.loginFacade, $("#login-message-wrapper", content));

      // Set events
      self.eventHandler();
    });
  },
  /**
      * Verificar si un dominio existe, solo se validan los dominios tomados al momento de cargar el sitio.
      * @param currentDomain
      * @returns {boolean}
      */
  isValidDomain: function isValidDomain(currentDomain) {
    return $.grep(this.domains || [], function (item) {
      return item.name === currentDomain;
    }).length > 0;
  },
  eventHandler: function eventHandler() {
    var self = this;
    var content = self.getContent();
    var userInput = $("#user", content);
    var passwordInput = $("#password", content);
    var domainInput = $("#domain", content);
    var oAuthParams = self.loginFacade.oAuthParameters || {};

    $("#user,#password", content).keypress(function (e) {
      if (e.which == 13) {
        $("#btn-login", content).click();
      }
    });

    /* Interval for set Info from Browser*/
    self.interval = setInterval(function () {
      var form = $('form');
      if (form[0].checkValidity()) {
        $("#btn-login", form).addClass('valid');
      } else {
        $("#btn-login", form).removeClass('valid');
      }
    }, 500);

    $("#btn-login", content).on("click", function (e) {
      $('#login-message-wrapper').text('');

      var element = e.target;
      if ($(element).hasClass('valid')) {
        var loginOption = $("input#rbSaveAccount:checked").attr("id");

        if (loginOption === undefined) {
          loginOption = "alwaysAsk";
        } else {
          loginOption = "saveAccount";
        }
        self.generalAuthentication({
          user: userInput.val(),
          password: passwordInput.val(),
          domain: domainInput.val(),
          loginOption: loginOption,
          type: oAuthParams.type || "",
          oAuth2InternalState: oAuthParams.oAuth2InternalState || "",
          keepAuthentication: oAuthParams.oauth2sso || "true" });

      }
    });

    /* Actions for smartphones y tabletas */
    if (bizagi.util.detectDevice() !== "desktop") {
      $('#user,#password', content).on('focus', function (e) {
        var element = e.target;
        $(element).closest('.bz-input-tm').find('.control-label').addClass('valid');
      });
      $('#user,#password', content).on('focusout', function (e) {
        var element = e.target;
        var val = element.value;
        if (val === '') {
          $(element).closest('.bz-input-tm').find('.control-label').removeClass('valid');
        }
      });
      $('#rbSaveAccount', content).on('change', function (e) {
        var element = e.target;
        e.target.checked;
        if (element.checked) {
          $(element).closest('.login-checkbox').addClass('checked');
        } else {
          $(element).closest('.login-checkbox').removeClass('checked');
        }
      });
    }

    userInput.bind("change input blur focus", self.enableDisableDomain.bind(self));

    userInput.click(function () {
      self.removeReadOnly(userInput, true);
    });
  },
  /**
      *
      * @param {jQuery} userInput
      * @param {boolean} [setFocus]
      */
  removeReadOnly: function removeReadOnly(userInput, setFocus) {
    if (userInput.length > 0 && userInput[0].hasAttribute('readonly')) {
      userInput.removeAttr('readonly');
      if (typeof setFocus === "undefined" || setFocus === true) {
        userInput.blur();
        userInput.focus();
      }
    }
  },
  enableDisableDomain: function enableDisableDomain() {
    var self = this;

    if (self.usernameContainsDomain()) {
      $(".domain-selector").hide();
    } else {
      $(".domain-selector").show();
    }
  },
  usernameContainsDomain: function usernameContainsDomain() {
    var self = this;
    var content = self.getContent();
    var userInput = $("#user", content);
    var userVal = userInput.val();
    return userVal && (userVal.indexOf("\\") !== -1 || userVal.indexOf("@") !== -1);
  },
  generalAuthentication: function generalAuthentication(params) {
    var self = this;

    if (params.user === "") {
      self.messageController.addErrorMessage(bizagi.localization.getResource("login-user-name-empty"));
    }
    if (params.password === "") {
      self.messageController.addErrorMessage(bizagi.localization.getResource("login-user-password-empty"));
    }

    if (self.usernameContainsDomain()) {
      if (params.user.indexOf('@') !== -1) {
        var userAndDomain = params.user.split(/@/);
        params.domain = userAndDomain[1];
        params.user = userAndDomain[0];
      } else if (params.user.indexOf('\\') !== -1) {
        var domainAndUser = params.user.split(/\\/);
        params.domain = domainAndUser[0];
        params.user = domainAndUser[1];
      }
    }

    if (!self.messageController.hasErrors()) {
      $.when(self.dataService.userLogin(params)).done(function (response) {
        var authType = self.params.authenticationType;
        if (response && response.credentialSuccessForMFA && (authType === "Bizagi" || authType === "BizAgi" || authType === "Mixed" || authType === "Windows")) {
          window.clearInterval(self.interval);
          self.dataService.paramsUser = params;
          self.loginFacade.changeWidget('BIZAGI_LOGIN_WIDGET_REQUESTCODE', {
            response: response });

          return;
        }

        if (bizagi.util.parseBoolean(response.isAuthenticate)) {
          window.clearInterval(self.interval);
          self.loginFacade.setBizagiAuthentication(response);
        } else {
          var param = "";
          if (response.paramError) {
            param = response.paramError;
          } else if (params.user) {
            param = params.user;
          }

          self.messageController.showWidgetCustomMessage(response.status, "error", [param]);
        }
      }).fail(function () {
        self.messageController.showWidgetCustomMessage("generic", "error");
      });

    } else {
      self.messageController.showErrors();
    }
  },
  getParameterByName: function getParameterByName(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  } });