/**
 * Bizagi login module
 * 
 * @author Edward J Morales
 */

/*
     *   Create login facade
     */
$.Class.extend("bizagi.login.facade", {}, {
  /* 
                                             *   Constructor
                                             */
  init: function init(params) {
    this.oAuthParameters = params.oAuthParameters || {};

    //used to allow the load of specific templates  
    this.loginFactory = new bizagi.login.factory();
    // Creates a data service instance
    this.dataService = new bizagi.login.services.service(params);
    // Set default params
    this.defaultParams = params || {};

    this.defaultWidget = "BIZAGI_LOGIN_WIDGET_LOGIN";
  },
  /*
      *   Start point method to use in the main javascript pages
      *   This method will process everything and attach the html directly to the dom
      */
  execute: function execute(canvas) {
    var self = this;
    var doc = this.ownerDocument;
    var body = $("body", doc);
    canvas = canvas || $("<div/>").appendTo(body);

    // Creates ready deferred        
    self.executionDeferred = new $.Deferred();
    $.when(self.render()).done(function (content) {
      // Replace canvas with content
      bizagi.util.replaceSelector(body, canvas, content);
      self.executionDeferred.resolve();
    });
  },
  /*
      *   Returns the execution deferred to determine if the component is ready or not
      */
  ready: function ready() {
    return this.executionDeferred.promise();
  },

  getDefaultWidget: function getDefaultWidget() {
    var self = this;
    var def = new $.Deferred();
    var oAuthParameters = self.oAuthParameters || {};
    var type = oAuthParameters.type || "";
    var oAuth2InternalState = oAuthParameters.oAuth2InternalState || "";

    // Get login configuration
    $.when(self.dataService.getConfiguration()).done(function (response) {
      if (response.redirectErrorPage) {
        window.location.href = response.redirectErrorPage;
        return;
      }

      self.loginConfig = response;
      var authenticationType = response.authenticationType;
      var isQuickLogin = bizagi.util.parseBoolean(response.isQuickLogin);
      var authenticationSubType = response.authenticationSubType;

      switch (authenticationType) {
        case 'Windows':
        case 'Custom':
        case 'Ldap':
          def.resolve(self.defaultWidget);
          break;
        case 'BizAgi':
        case 'Mixed':
          self.authenticatorWithQuickLogin(isQuickLogin, def);
          break;
        case 'SAML':
          $.when(self.dataService.getUserSamlSession("")).done(function (responseSaml) {

            if (typeof responseSaml.urlRedirect != 'undefined') {
              window.location.href = responseSaml.urlRedirect;
            } else
            {
              if (responseSaml.isAuthenticate == 'true') {
                self.setBizagiAuthenticationSaml(responseSaml);
              } else
              if (responseSaml.isAuthenticate == 'false') {
                //Redirection to SAML
                loader.nativeAjax(loader.getPathUrl("saml2/login"), function (response) {
                  var jsonRedirect = JSON.parse(response.responseText);
                  window.location.href = jsonRedirect.urlRedirect;
                }).
                fail(function () {
                  self.renderWidget(widget, params, def);
                });
              }
            }
          });
          break;
        case 'Federate':
          self.federateAuthenticator(response, authenticationSubType, oAuth2InternalState);
          break;
        case 'OAuth2':
          self.oauth2Authenticator(oAuth2InternalState, def);
          break;
        case 'Multiple':
          def.reject(); // cancel load of login normal

          bizagi.renderLoginMultiple({
            proxyPrefix: bizagiConfig.proxyPrefix || "",
            oAuthParameters: oAuthParameters,
            runMultipleAuthenticationType: self.runMultipleAuthenticationType(self) },
          true);

          break;
        default: //'Custom''IntegratedWindowsAuthentication''IntegratedOraclePortal''LDAPAuthentication''OAuth'
          self.authenticatorWithQuickLogin(isQuickLogin, def);
          break;}


    }).fail(function (message) {
      console.log(message);
    });

    return def.promise();
  },
  runMultipleAuthenticationType: function runMultipleAuthenticationType(self) {
    var oAuthParameters = self.oAuthParameters || {};
    // var type = oAuthParameters.type || "";
    var oAuth2InternalState = oAuthParameters.oAuth2InternalState || '';
    var reg = /__domain__(.*)__ed__/;

    var rcheck = function rcheck(domain) {
      var def = new $.Deferred();

      $.when(self.dataService.getMultipleAuthenticationConfiguration(domain)).done(function (multipleAuthConfig) {
        switch (multipleAuthConfig.multipleAuthenticationType.toLowerCase()) {
          case 'windows':
          case 'custom':
          case 'ldap':
            def.resolve(self.defaultWidget);
            break;
          case 'bizagi':
            self.authenticatorWithQuickLogin(bizagi.util.parseBoolean(multipleAuthConfig.isQuickLogin), def);
            break;
          case 'federated':
            self.federateAuthenticator(multipleAuthConfig, multipleAuthConfig.multipleAuthenticationSubType, oAuth2InternalState, domain);
            break;
          case 'oauth2':
            self.oauth2Authenticator(oAuth2InternalState, domain, def);
            break;
          default:
            self.authenticatorWithQuickLogin(bizagi.util.parseBoolean(multipleAuthConfig.isQuickLogin), def);
            break;}

      }).fail(function (message) {
        console.log(message);
        def.reject(message);
      });

      return def.promise();
    };

    if (reg.test(oAuth2InternalState)) {
      var match = oAuth2InternalState.match(reg);
      var domain = { domain: match[1] };
      rcheck(domain);
    }

    return rcheck;
  },
  authenticatorWithQuickLogin: function authenticatorWithQuickLogin(isQuickLogin, def) {
    var self = this;

    if (isQuickLogin) {
      def.resolve("BIZAGI_LOGIN_WIDGET_QUICKLOGIN");
    } else {
      def.resolve(self.defaultWidget);
    }
  },
  federateAuthenticator: function federateAuthenticator(response, authenticationSubType, oAuth2InternalState, domain) {
    var self = this;
    var federationLoginBindingProtocol = response.federationLoginBindingProtocol;
    var oAuthParams = self.oAuthParameters || {};
    var syncToken = oAuthParams.syncToken;
    var type = oAuthParams.type;

    if (domain) {
      domain = domain.domain;
    } else
    {
      domain = "";
    }

    if (authenticationSubType) {
      authenticationSubType = authenticationSubType.toLowerCase();
    }

    if (authenticationSubType === 'wsfederated') {
      if (type == 'oauth2AsBridge' && syncToken != 'undefined') {
        $.when(self.dataService.getOAuth2AuthFromSyncToken(syncToken)).done(function (res) {

          var hrefer = sessionStorage.getItem(oAuth2InternalState) || window.document.referrer;

          if (res.isAuthenticate == 'true') {
            var params = {};
            params.loginOption = "alwaysAsk";
            params.accessToken = res.accessToken;
            params.type = type;
            params.oAuth2InternalState = oAuth2InternalState;

            if (res.domainAndUsername) {
              params.domain = res.domainAndUsername.split("\\")[0];
              params.user = res.domainAndUsername.split("\\")[1];
            }

            $.when(self.dataService.userLogin(params)).done(function (responseLogin) {
              if (responseLogin.isAuthenticate == 'true') {
                self.setBizagiAuthentication(responseLogin);
              } else
              {
                var message = bizagi.localization.getMessageFromLocalization(responseLogin.status, "error", [responseLogin.paramError]);
                window.location.href = 'auth-error.html?error=' + responseLogin.status + '&error_description=' + message + '&http_status_code=' + responseLogin.httpStatusCode + '&hrefer=' + hrefer;
              }
            }).fail(function (responseError) {
              var message = bizagi.localization.getMessageFromLocalization(responseError.status, "error", [responseError.paramError]);
              window.location.href = 'auth-error.html?error=' + responseError.status + '&error_description=' + message + '&http_status_code=' + responseError.httpStatusCode + '&hrefer=' + hrefer;
            });
          } else
          if (res.isAuthenticate == 'false') {
            def.resolve(self.defaultWidget);
          } else
          if (res.status != 'SUCCESS') {
            var message = bizagi.localization.getMessageFromLocalization(res.status, "error", [res.paramError]);
            window.location.href = 'auth-error.html?error=' + res.status + '&error_description=' + message + '&http_status_code=' + res.httpStatusCode + '&hrefer=' + hrefer;
          }

          sessionStorage.removeItem(oAuth2InternalState);
        });
      } else
      if (type == 'oauth2AsServer') {
        var url = response.homeRealmDiscoveryURL;
        var wctx = '__OAuIntSta__' + oAuth2InternalState;

        if (url.indexOf('wctx=') >= 0) {
          url = url.replace(/wctx=[^&]*/, 'wctx=' + wctx);
        } else
        {
          url = url + '&wctx=' + wctx;
        }

        window.location.href = url;
      }
    } else
    if (authenticationSubType === 'saml') {
      if (type == 'oauth2AsBridge' && syncToken != 'undefined') {
        $.when(self.dataService.getOAuth2AuthFromSyncToken(syncToken)).done(function (res) {

          var hrefer = sessionStorage.getItem(oAuth2InternalState) || window.document.referrer;
          if (res.isAuthenticate == 'true') {
            var params = {};
            params.loginOption = "alwaysAsk";
            params.accessToken = res.accessToken;
            params.type = type;
            params.oAuth2InternalState = oAuth2InternalState;

            if (res.domainAndUsername) {
              params.domain = res.domainAndUsername.split("\\")[0];
              params.user = res.domainAndUsername.split("\\")[1];
            }

            $.when(self.dataService.userLogin(params)).done(function (responseLogin) {
              if (responseLogin.isAuthenticate == 'true') {
                self.setBizagiAuthentication(responseLogin);
              } else
              {
                var message = bizagi.localization.getMessageFromLocalization(responseLogin.status, "error", [responseLogin.paramError]);
                var url = 'auth-error.html?error=' + responseLogin.status + '&error_description=' + message + '&http_status_code=' + responseLogin.httpStatusCode + '&hrefer=' + hrefer;
                if (responseLogin.domainAndUsername) {
                  url = url + '&domainAndUsername=' + responseLogin.domainAndUsername;
                }

                window.location.href = url;
              }
            }).fail(function (responseError) {
              var message = bizagi.localization.getMessageFromLocalization(responseError.status, "error", [responseError.paramError]);

              var url = 'auth-error.html?error=' + responseError.status + '&error_description=' + message + '&http_status_code=' + responseError.httpStatusCode + '&hrefer=' + hrefer;
              if (responseLogin.domainAndUsername) {
                url = url + '&domainAndUsername=' + responseLogin.domainAndUsername;
              }

              window.location.href = url;
            });
          } else
          if (res.isAuthenticate == 'false') {
            def.resolve(self.defaultWidget);
          } else
          if (res.status != 'SUCCESS') {
            var message = bizagi.localization.getMessageFromLocalization(res.status, "error", [res.paramError]);
            window.location.href = 'auth-error.html?error=' + res.status + '&error_description=' + message + '&http_status_code=' + res.httpStatusCode + '&hrefer=' + hrefer;
          }

          sessionStorage.removeItem(oAuth2InternalState);
        }).fail(function (responseError) {
          console.log(responseError);
          responseError = responseError || {};
          var message = bizagi.localization.getMessageFromLocalization(responseError.status || 'error', "error", [responseError.paramError]);

          var url = 'auth-error.html?error=' + responseError.status + '&error_description=' + message + '&http_status_code=' + responseError.httpStatusCode + '&hrefer=' + hrefer;
          if (responseLogin.domainAndUsername) {
            url = url + '&domainAndUsername=' + responseLogin.domainAndUsername;
          }

          window.location.href = url;
        });
      } else
      if (type == 'oauth2AsServer') {
        var relayState = '__OAuIntSta__' + oAuth2InternalState;
        var params = { state: relayState, domain: domain };
        $.when(self.dataService.getFederateLoginUrl(params)).done(function (urlResponse) {
          var url = urlResponse.federateLoginUrl;

          if (federationLoginBindingProtocol === 'POST') {
            window.document.open();
            window.document.write(url);
            window.document.close();
          } else
          {
            window.location.href = url;
          }
        }).fail(function (responseError) {
          console.log(responseError);
          responseError = responseError || {};
          var message = bizagi.localization.getMessageFromLocalization(responseError.status || 'connection', "error", [responseError.paramError]);

          var url = 'auth-error.html?error=' + responseError.status + '&error_description=' + message + '&http_status_code=' + responseError.httpStatusCode + '&hrefer=' + hrefer;
          if (responseLogin.domainAndUsername) {
            url = url + '&domainAndUsername=' + responseLogin.domainAndUsername;
          }

          window.location.href = url;
        });
      }
    }
  },
  oauth2Authenticator: function oauth2Authenticator(oAuth2InternalState, domain, def) {
    var self = this;
    var oAuthParams = self.oAuthParameters || {};
    var syncToken = oAuthParams.syncToken;
    var type = oAuthParams.type;

    if (domain) {
      domain = domain.domain;
    } else
    {
      domain = "";
    }

    if (type == 'oauth2AsBridge' && syncToken != 'undefined') {
      $.when(self.dataService.getOAuth2AuthFromSyncToken(syncToken)).done(function (res) {

        var hrefer = sessionStorage.getItem(oAuth2InternalState) || window.document.referrer;

        if (res.isAuthenticate == 'true') {
          var params = {};
          params.loginOption = "alwaysAsk";
          params.accessToken = res.accessToken;
          params.type = type;
          params.oAuth2InternalState = oAuth2InternalState;

          if (res.domainAndUsername) {
            params.domain = res.domainAndUsername.split("\\")[0];
            params.user = res.domainAndUsername.split("\\")[1];
          }

          $.when(self.dataService.userLogin(params)).done(function (responseLogin) {
            if (responseLogin.isAuthenticate == 'true') {
              self.setBizagiAuthentication(responseLogin);
            } else
            {
              var message = bizagi.localization.getMessageFromLocalization(responseLogin.status, "error", [responseLogin.paramError]);
              window.location.href = 'auth-error.html?error=' + responseLogin.status + '&error_description=' + message + '&http_status_code=' + responseLogin.httpStatusCode + '&hrefer=' + hrefer;
            }
          }).fail(function (responseError) {
            var message = bizagi.localization.getMessageFromLocalization(responseError.status, "error", [responseError.paramError]);
            window.location.href = 'auth-error.html?error=' + responseError.status + '&error_description=' + message + '&http_status_code=' + responseError.httpStatusCode + '&hrefer=' + hrefer;
          });
        } else
        if (res.isAuthenticate == 'false') {
          def.resolve(self.defaultWidget);
        } else
        if (res.status != 'SUCCESS') {
          var message = bizagi.localization.getMessageFromLocalization(res.status, "error", [res.paramError]);
          if (message.startsWith('workportal-general-error') && res.message)
          {
            message = res.message;
          }
          window.location.href = 'auth-error.html?error=' + res.status + '&error_description=' + message + '&http_status_code=' + res.httpStatusCode + '&hrefer=' + hrefer;
        }

        sessionStorage.removeItem(oAuth2InternalState);
      });
    } else
    if (type == 'oauth2AsServer') {
      var data = {};
      data["state"] = '__OAuIntSta__' + oAuth2InternalState;
      data["domain"] = domain;
      $.when(self.dataService.getOAuth2RedirectAuthorizacionDataAsServer(data)).done(function (res) {
        if (res.status == 'SUCCESS') {
          //Redirection to OAuth2 Authorization Server
          var url = res.authorizationUrl + '?response_type=' + res.responseType + '&client_id=' + res.clientId + '&redirect_uri=' + res.redirectUri + '&scope=' + res.scope;
          if (res.isSingleSignOnCookieEnable) {
            url += '&sso=' + res.isSingleSignOnCookieEnable;
          }
          if (res.state) {
            url += '&state=' + res.state;
            sessionStorage.setItem(oAuth2InternalState, window.document.referrer);
          }
          if (res.nonce) {
            url += '&nonce=' + res.nonce;
          }
          if (res.response_mode) {
            url += '&response_mode=' + res.response_mode;
          }

          window.location.href = url;
        } else
        if (res.status == 'ERROR') {
          window.location.href = 'auth-error.html?error=' + res.error + '&error_description=' + res.errorDescription + '&http_status_code=' + res.httpStatusCode + '&state=' + res.state + '&hrefer=' + window.document.referrer;
        }
      }).fail(function (err) {
        def.reject(err && err.responseText ? err.responseText : err);
      });
    }
  },
  /**
      *
      */
  render: function render(widget, params) {

    var self = this;
    var def = new $.Deferred();

    self.session = {};

    $.when(self.dataService.getConfiguration()).done(function (responseConfig) {
      if (responseConfig.redirectErrorPage) {
        window.location.href = responseConfig.redirectErrorPage;
        return;
      }

      var isRemoteUser = responseConfig.isRemoteUser;
      self.loginConfig = responseConfig;
      self.session.isProduction = responseConfig.isProduction;
      var oAuthParameters = self.oAuthParameters || {};

      //Ensures if the Bizagi
      if (self.loginConfig.authenticationType == 'BizAgi')
      self.loginConfig.isBizagiAuthentication = true;

      var data = {};
      data["internalState"] = oAuthParameters.oAuth2InternalState || "";
      data["source"] = 'loginexternal';

      $.when(self.dataService.readUserCookies(data)).done(function (response) {
        var loginOption = response.loginOption;

        self.session.user = response.userName;
        self.session.domain = response.domain;
        self.session.selectedCheck = response.loginOption;

        if (loginOption == "saveAccountPassword") {
          //force login
          if (bizagi.util.parseBoolean(response.isAuthenticaded)) {
            $.when(self.dataService.getCurrentUser("")).done(function (responseLogin) {
              responseLogin.isAuthenticate = "true";
              responseLogin.shortDateFormat = responseLogin.shortDateFormat;
              responseLogin.longDateFormat = responseLogin.longDateFormat;
              responseLogin.timeFormat = responseLogin.timeFormat;

              if (window.sessionStorage && response.XSRFToken) {
                window.sessionStorage.setItem("XSRFToken", response.XSRFToken);
              }

              self.setBizagiAuthentication(responseLogin, false);
              self.renderWidget(widget, params, def);
            }).fail(function () {
              self.renderWidget(widget, params, def);
            });
          } else {
            self.renderWidget(widget, params, def);
          }
        } else if (loginOption == "saveAccount") {
          self.renderWidget(widget, params, def);

        } else if (loginOption == "formsAuthentication") {
          if (bizagi.util.parseBoolean(response.isAuthenticaded)) {
            $.when(self.dataService.getCurrentUser("")).done(function (responseLogin) {
              responseLogin.isAuthenticate = "true";
              responseLogin.shortDateFormat = responseLogin.shortDateFormat;
              responseLogin.longDateFormat = responseLogin.longDateFormat;
              responseLogin.timeFormat = responseLogin.timeFormat;

              if (window.sessionStorage && response.XSRFToken) {
                window.sessionStorage.setItem("XSRFToken", response.XSRFToken);
              }

              self.setBizagiAuthentication(responseLogin, false);
              self.renderWidget(widget, params, def);
            }).fail(function () {
              self.renderWidget(widget, params, def);
            });
          } else
          {
            if (response.errorUrl) {
              window.location.href = response.errorUrl;
            } else
            if (response.formsAuthenticationRedirectLoginPage) {
              window.location.href = response.formsAuthenticationRedirectLoginPage;
            } else
            {
              self.renderWidget(widget, params, def);
            }
          }
        } else if (loginOption == "cookieSSOAuthentication") {
          if (bizagi.util.parseBoolean(response.isAuthenticaded)) {
            if (window.sessionStorage && response.XSRFToken) {
              window.sessionStorage.setItem("XSRFToken", response.XSRFToken);
            }

            self.setBizagiAuthentication(response, false);
            self.renderWidget(widget, params, def);
          } else
          {
            if (response.errorUrl) {
              window.location.href = response.errorUrl;
            } else
            if (response.formsAuthenticationRedirectLoginPage) {
              window.location.href = response.formsAuthenticationRedirectLoginPage;
            } else
            {
              self.renderWidget(widget, params, def);
            }
          }
        } else
        {
          self.session.user = "";
          self.session.domain = "";
          self.session.selectedCheck = "rbAskAlways";
          self.renderWidget(widget, params, def);
        }

      }).fail(function (error) {
        //errorBizagi 
        var errorValue = JSON.parse(error.responseText);
        console.log(errorValue.message + "\n" + errorValue.innerExceptions);
      });
    }).fail(function (error) {
      //errorBizagi
      if (error.responseText.indexOf('User Not Valid') != -1) {
        window.location.href = "App/Inicio/UserNotValid.aspx";
      } else
      {
        var errorValue = JSON.parse(error.responseText);
        if (errorValue.code == "AUTHENTICATION_ERROR") {
          window.location.href = "App/Inicio/UserNotValid.aspx";
        } else
        {
          console.log(errorValue.message + "\n" + errorValue.innerExceptions);
        }
      }

    });
    return def;
  },
  renderWidget: function renderWidget(widget, params, def) {
    var self = this;
    if (!widget) {
      $.when(self.getDefaultWidget()).done(function (widgetResponse) {
        widget = widgetResponse;
        return self.renderWidgetWait(widget, params, def);
      });
    } else {
      return self.renderWidgetWait(widget, params, def);
    }
    return def.promise();
  },
  renderWidgetWait: function renderWidgetWait(widget, params, def) {
    var self = this;

    $.when(widget, self.loginFactory.loadTemplates()).done(function (widget) {
      var widgetReference = self.getWidgetObject(widget, self, self.dataService);

      // Extend Params
      params = params || {};
      params = $.extend(params, self.loginConfig);
      params = $.extend(params, self.session);

      // Render content
      $.when(widgetReference.renderContent(params)).done(function () {
        // Call post-render method

        widgetReference.postRender(params);
        // Resolve deferred
        def.resolve(widgetReference.getContent());
      });
    });
    return def.promise();
  },

  getWidgetObject: function getWidgetObject(widget, facade, dataService) {
    switch (widget) {
      case "BIZAGI_LOGIN_WIDGET_QUICKLOGIN":
        return new bizagi.login.widgets.quicklogin(facade, dataService);
        break;
      case "BIZAGI_LOGIN_WIDGET_REQUESTCODE":
        return new bizagi.login.widgets.requestcode(facade, dataService);
        break;
      default:
        return new bizagi.login.widgets.login(facade, dataService);
        break;}

  },

  changeWidget: function changeWidget(widget, params) {
    var self = this;
    $.when(self.render(widget, params)).done(function (content) {
      var body = $("body");
      // Empty all content
      body.empty();

      var canvas = $("<div/>").appendTo(body);
      bizagi.util.replaceSelector($("body"), canvas, content);
    });
  },

  /*
      *   Method to fetch templates from a private dictionary
      */
  getTemplate: function getTemplate(template) {
    return this.loginFactory.getTemplate(template);
  },
  /**
      *
      * @param params
      */
  setBizagiAuthentication: function setBizagiAuthentication(params, ifErrorReload) {
    if (ifErrorReload === undefined) {
      ifErrorReload = true;
    }

    var self = this;
    params.isProduction = self.session.isProduction;
    var oAuthParameters = self.oAuthParameters || {};
    var type = oAuthParameters.type || "";
    if (type && (type == 'oauth2AsServer' || type == 'oauth2AsBridge')) {
      var code = params.code;
      if (code) {
        //Send message
        if (bizagiWebView) {
          bizagiWebView.postMessage(params, bizagiWebView.location.origin);
        } else {
          window.parent.postMessage(params, window.location.origin);
        }

        var callbackUri = params.callbackUri;
        var state = encodeURIComponent(params.state);
        if (params.callbackUriStrategy == 'MobileTitleBarBrowser') {
          window.location = callbackUri + '?oAuth2InternalState=' + params.internalState;
        } else {
          window.location = callbackUri + '?code=' + code + '&state=' + state;
        }
      } else {
        if (ifErrorReload === true) {
          location.reload();
        } else
        {
          return false;
        }
      }
    } else {
      sessionStorage.setItem("bizagiAuthentication", JSON.encode(params));
      // Refresh page in order to load workportal module
      if (ifErrorReload === true) {
        location.reload();
      } else
      {
        return false;
      }
    }
  },
  /**
      *
      * @param params
      */
  setBizagiAuthenticationSaml: function setBizagiAuthenticationSaml(params) {
    params.isProduction = this.session.isProduction;
    sessionStorage.setItem("bizagiAuthentication", JSON.encode(params));
    // Refresh page in order to load workportal module
    location.reload();
  },
  /**
      *
      * @param params
      */
  setOAuth2Authentication: function setOAuth2Authentication(params) {
    params.isProduction = this.session.isProduction;
    sessionStorage.setItem("bizagiAuthentication", JSON.encode(params));
    // Refresh page in order to load workportal module
    var url = window.location.href;
    var parts = url.split('?');
    window.location.href = parts[0];
  } });