/**
 * Services for login
 */

$.Class.extend("bizagi.login.services.service", {}, {
  /**
                                                      * Constructor
                                                      */
  init: function init(params) {
    this.serviceLocator = new bizagi.render.services.context(params);
  },
  /**
     * Get bizagi configuration
     * @param {Function}   type    callback function when the file load is succed
     * @return {deferred} ajax object with JSON content
     */
  getConfiguration: function getConfiguration() {
    // Check session storage
    var bizagiConfig = window.sessionStorage.getItem("BizagiConfiguration");
    if (bizagiConfig) {
      var def = new $.Deferred();
      def.resolve(JSON.parse(bizagiConfig));
      return def.promise();
    } else {
      var self = this;
      var url = self.serviceLocator.getUrl("authenticationConfig");

      return $.read(url);
    }
  },
  /**
     * Login user 
     * @loginParams {object} loginParams Object with login credentials
     * @return {deferred} ajax object with JSON content
     */
  userLogin: function userLogin(loginParams) {
    var self = this;
    var url = self.serviceLocator.getUrl("oauth2IdPServerLoginPage");
    var type = loginParams.type || "";

    if (type && (type == 'oauth2AsServer' || type == 'oauth2AsBridge')) {
      var oAuth2InternalState = loginParams.oAuth2InternalState || "";
      if (oAuth2InternalState) {
        loginParams.oAuth2InternalState = oAuth2InternalState;
        url = self.serviceLocator.getUrl("oauth2IdPServerLoginPage");
      }
    }

    //Call to the restfull service for authentication
    return $.create(url, loginParams);
  },
  /**
     * Get json with bizagi domains list
     * @return {deferred} ajax object with JSON content
     */
  getDomainList: function getDomainList() {
    var self = this;
    var url = self.serviceLocator.getUrl("authenticationDomains");

    return $.read(url);
  },
  /**
     * Unlock user account 
     *  
     * @param {object} loginParams Object with login credentials
     * @return {deferred} ajax object with JSON content
     */
  unlockAccount: function unlockAccount(loginParams) {
    var self = this;
    var data = {};
    var url = self.serviceLocator.getUrl("unlockAccount");

    // Set password information on data request for security reasons
    data.domain = loginParams.domain;
    data.user = loginParams.user;
    data.password = loginParams.password;
    data.observation = loginParams.observation;

    return $.create(url, data);
  },
  /**
     * Get list of bizagi users
     * 
     * @return {deferred} ajax object with JSON content
     */
  getQuickLoginUserList: function getQuickLoginUserList() {
    var self = this;
    var url = self.serviceLocator.getUrl("quickLoginUsers");

    return $.read(url);

  },
  /**
     * Unlock user account 
     *  
     * @param {object} loginParams Object with login credentials
     * @return {deferred} ajax object with JSON content
     */
  changePassword: function changePassword(loginParams) {
    var self = this;
    var data = {};
    var url = self.serviceLocator.getUrl("changePassword");

    // Set password information on data request for security reasons
    data.domain = loginParams.domain;
    data.user = loginParams.user;
    data.password = loginParams.password;
    data.newPassword = loginParams.newPassword;
    data.passwordConfirmation = loginParams.passwordConfirmation;

    data.secretQuestion = loginParams.secretQuestion;
    data.secretAnswer = loginParams.secretAnswer;

    return $.create(url, data);
  },

  /*
     *
     */
  forgotPassword: function forgotPassword(userParams) {
    var self = this;
    var data = {};
    var url = self.serviceLocator.getUrl("forgotPassword");

    data.secretAnswer = userParams.secretAnswer;
    data.domain = userParams.domain;
    data.user = userParams.user;

    return $.create(url, data);
  },

  /**
      * make request for user's secret question on forget password
      * please have in mind this method exists in another files if you change me, update them
      * /login_multiple/
      * /login/
      * @param {JSON} userParams 
      */
  secretQuestion: function secretQuestion(userParams) {
    var self = this;
    var url = self.serviceLocator.getUrl("secretQuestion");

    return $.create(url, userParams);
  },

  /*
     *   Return the state of the login cookie
     */
  readUserCookies: function readUserCookies(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("readUserCookies");
    return $.read(url, params);
  },
  /*
     *   Gets the current working user
     */
  getCurrentUser: function getCurrentUser(params) {
    var self = this;

    // Call ajax and returns promise
    return $.read(self.serviceLocator.getUrl("userHandlerGetCurrentUser"));
  },
  getUserSamlSession: function getUserSamlSession(params) {
    var self = this;
    return $.read(self.serviceLocator.getUrl("userSamlSession"));
  },
  getOAuth2RedirectAuthorizacionData: function getOAuth2RedirectAuthorizacionData(params) {
    var self = this;
    return $.read(self.serviceLocator.getUrl("oauth2RedirectAuthorizacionData"), params);
  },
  getOAuth2RedirectAuthorizacionDataAsServer: function getOAuth2RedirectAuthorizacionDataAsServer(params) {
    var self = this;
    return $.read(self.serviceLocator.getUrl("oauth2RedirectAuthorizacionDataAsServer"), params);
  },
  getOAuth2AuthFromSyncToken: function getOAuth2AuthFromSyncToken(params) {
    var self = this;
    var data = {};
    data.syncToken = params;

    return $.create(self.serviceLocator.getUrl("oauth2AuthSynctoken"), data);
  },
  oauth2SSOValidateSession: function oauth2SSOValidateSession(params) {
    var self = this;
    return $.create(self.serviceLocator.getUrl("oauth2IdPServerValidateSession"), params);
  },
  isAuthenticatedWithPrincipalInjection: function isAuthenticatedWithPrincipalInjection(params) {
    var self = this;
    return $.read(self.serviceLocator.getUrl("isAuthenticatedWithPrincipalInjection"), params);
  },
  getFederateLoginUrl: function getFederateLoginUrl(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("federateLoginUrl");

    return $.read(url, params);
  },

  getLogo: function getLogo() {
    var self = this;
    return $.read({
      url: self.serviceLocator.getUrl("logo") }).
    pipe(function (data) {
      var result = {};
      if (typeof data.logo != "undefined") {
        try {
          var logoObj = JSON.parse(data.logo);
          result.logo = logoObj.login;
        } catch (e) {
          //  Nothing todo
        }
      }
      return result;
    });
  },

  /**
      *
      * @param {{ code: number }} params
      */
  verifyCode: function verifyCode(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("verifyCode");

    return $.create(url, params);
  },

  /**
      *
      * @param {{providerType: 'SMS' | 'email'}} params
      */
  resendVerifyCode: function resendVerifyCode(params) {
    var self = this;
    var url = self.serviceLocator.getUrl("resendVerifyCode");
    return $.create(url, params);
  },
  /**
      *
      * @param { { domain: string } } domain
      * @returns {*}
      */
  getMultipleAuthenticationConfiguration: function getMultipleAuthenticationConfiguration(domain) {
    var self = this;
    return $.read(self.serviceLocator.getUrl("multipleAuthenticatorConfig"), domain).pipe(function (response) {
      window.sessionStorage.setItem("BizagiConfiguration", JSON.stringify(response));
      return response;
    });
  } });